/**
 * http
 */
import axios from 'axios';
import { message } from 'antd';

/**
 * get
 * @param url       
 * @param msg       
 * @param headers   header
 */
export const get = ({url, msg = '', headers}) =>
    axios.get(url, headers).then(res => res.data).catch(err => {
       console.log(err);
       message.warn(msg);
    });

/**
 * post
 * @param url       
 * @param data      
 * @param msg       
 * @param headers   header
 */
export const post = ({url, data, msg = '', headers}) =>
    axios.post(url, data, headers).then(res => res.data).catch(err => {
        console.log(err);
        message.warn(msg);
    });
